import Vue from 'vue';
import VueRouter from 'vue-router';
import app from './App';
import vheader from 'component/mui/Header';

Vue.use(VueRouter);
var router = new VueRouter();

// head
var HeaderVue = Vue.extend(vheader);
router.header = new HeaderVue({
    el: 'vheader',
    propsData: {
        hdstyle: {
            back: {
                display: true
            },
            title: {
                display: true
            },
            rightSearchBtn: {
                display: true
            },
            searchBar: {
                display: 'none',
                placeholder: '搜索公告',
                modelName: 'SellerBulletin'
            }
        },
        searchValue: ''
    },
    watch: {
        searchValue() {
            this.hdstyleDef.rightSearchBtn.display = 'none';
            this.hdstyleDef.searchBar.display = true;
            this.typeclass = 'commodity';
            if (typeof router.app === 'object') {
                router.app.searchValue = this.searchValue;
                router.app.getData();
            }
        }
    }
});

mui.init({
    swipeBack: true,
    subpages: [{
        url: 'offline.html', 
        id: 'bulletin-offline',
        styles: {
            top: '45px',
            bottom: '0px'
        },
        show:{
            autoShow:false,
            aniShow:'slide-in-right',
            duration:100
        }
    }]
});

mui.plusReady(function() {
    router.start(app, '#app');
    window.addEventListener('bulletin-refresh', () => {
        router.app.getData();
    });
});